<template>
  <div class="inverted"></div>
</template>
<script>
export default {
  props: {
    speed: {
      type: Number,
      default: 0.25
    },
    scale: {
      type: Number,
      default: 1
    },
    waves: {
      type: Number,
      default: 10
    },
    height: {
      type: Number,
      default: 0.5
    },
    src: {
      type: String
    }
  },
  data() {
    return {
      image: ""
    };
  },
  watch: {
    src: v => {
      console.log(v);
    }
  },
  mounted() {
    this.createImg(this.src);
  },
  methods: {
    createImg(src) {
      // let img = new Image();
      // img.src = require(`../assets/images/${test.jpg}`);
      // img.style.width = "100%";
      // img.style.height = "100%";
      // img.style.position = "absolute";
      // img.style.background =
      //   "linear-gradient(to bottom,#080339,#050d2b,#040416)";
      // document.body.appendChild(img);
      // this.image = img;
      this.init();
    },
    init() {
      const { waves, speed, scale, height, image } = this;
      let cvs = document.createElement("canvas");
      cvs.style.top = 0;
      let c = cvs.getContext("2d");

      let imgLoad = false;
      image.parentNode.insertBefore(cvs, image);
      let w = document.body.offsetWidth,
        h = document.body.offsetHeight,
        dw,
        dh;

      let offset = 0,
        frame = 0,
        max_frames = 0,
        frames = [];
      image.onload = () => {
        c.save();

        c.canvas.width = image.width;
        c.canvas.height = image.height * 2;
        c.canvas.style.zIndex = "5";

        c.scale(1, -1);
        c.drawImage(image, 0, -image.height * 2);
        c.drawImage(image, 0, image.height / 2);

        imgLoad = true;

        c.restore();

        w = c.canvas.width;
        h = c.canvas.height;
        dw = w;
        dh = h * height;

        var id = c.getImageData(0, h * height, w, h).data; // 获取图片像素点阵色彩信息
        var end = false;
        c.save();
        while (!end) {
          var odd = c.getImageData(0, h * height, w, h);
          var od = odd.data;
          var pixel = 0;
          for (var y = 0; y < dh; y++) {
            // 对二维数组进行遍历
            for (var x = 0; x < dw; x++) {
              // 模拟正弦波 来计算像素点的偏移位置
              var displacement =
                (scale * 10 * Math.sin(dh / (y / waves) + -offset)) | 0;
              var j = ((displacement + y) * w + x + displacement) * 4;
              if (j < 0) {
                pixel += 4;
                continue;
              }
              var m = j % (w * 4);
              var n = scale * 10 * (y / waves);
              if (m < n || m > w * 4 - n) {
                var sign = y < w / 2 ? 1 : -1;
                od[pixel] = od[pixel + 4 * sign];
                od[++pixel] = od[pixel + 4 * sign];
                od[++pixel] = od[pixel + 4 * sign];
                od[++pixel] = od[pixel + 4 * sign];
                ++pixel;
                continue;
              }

              if (id[j + 3] != 0) {
                od[pixel] = id[j];
                od[++pixel] = id[++j];
                od[++pixel] = id[++j];
                od[++pixel] = id[++j];
                ++pixel;
              } else {
                od[pixel] = od[pixel - w * 4];
                od[++pixel] = od[pixel - w * 4];
                od[++pixel] = od[pixel - w * 4];
                od[++pixel] = od[pixel - w * 4];
                ++pixel;
              }
            }
          }

          if (offset > speed * (6 / speed)) {
            offset = 0;
            max_frames = frame - 1;
            frame = 0;
            end = true;
          } else {
            offset += speed;
            frame++;
          }
          frames.push(odd);
        }
        c.restore();
        if (!image) {
          c.height = c.height / 2;
        }
      };

      setInterval(function() {
        if (imgLoad) {
          if (!image) {
            c.putImageData(frames[frame], 0, 0);
          } else {
            c.putImageData(frames[frame], 0, h * height);
          }
          if (frame < max_frames) {
            frame++;
          } else {
            frame = 0;
          }
        }
      }, 33.33);
      document.body.removeChild(image);
    }
  }
};
</script>
<style lang="less" scoped></style>
